	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>« En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; Ainsi donc, frères, aspirez au don de prophétie, et n'empêchez pas de parler en langues. Mais que tout se fasse avec bienséance et avec ordre. » <br><small>- 1 Corinthiens 14:27,39-40</small></p>\n</blockquote>\n<p>La Parole de Dieu est très claire ici :</p>\n<ol>\n<li>Pas plus de 2 ou 3 personnes maximum</li>\n<li>Elles doivent s'exprimer chacune à leur tour</li>\n<li>Quelqu'un doit interpréter</li>\n</ol>\n<blockquote>\n<p>« Que tout se fasse avec bienséance et avec ordre. (v.40) car Dieu n'est pas un Dieu de désordre, mais de paix. (v.33) »</p>\n</blockquote>\n<p>En d'autres termes, si le \"parler en langue\" est pratiqué d'une autre façon que celle-ci dans une église et qu'il en résulte de la confusion, Dieu n'en est pas l'auteur.</p>","frontmatter":{"title":"Comment le don des langues doit-il être pratiqué dans l'église ?"}}},"pathContext":{"slug":"/comment-le-don-des-langues-doit-il-etre-pratique-dans-l-eglise/"}}

